@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.2/css/all.min.css');

/* ==========================
   Base / Default styles
   (keep core layout and component rules here)
   ========================== */

body {
  margin: 0 auto;
  width: 100vw;
  min-width: 320px;
  max-width: 100%;
  min-height: 100svh;
  background: #EEEEEE;
}

.main-container {
  margin: 0 auto;
  box-sizing: border-box;
  min-height: 100svh;
  position: relative;
  width: 100%;
  max-width: 100%;
}

.logo-header {
  width: 50%;
  max-width: 300px;
  display: block;
  padding-top: 5%;
  padding-bottom: 5%;
  margin-left: auto;
  margin-right: auto;
  transition: transform 0.2s;
  align-items: center;
}

.youtube-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  align-items: stretch;
  margin-bottom: 3%;
}

.youtube-video {
  max-width: 500px;
  width: 100%;
  min-width: 300px;
  flex: 1 1 45%; /* 2つ並ぶように調整 */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
}

.youtube-video iframe {
  width: 100%;
  height: 280px;
  border: none;
  background: #000;
}

.header-nav {
  display: flex;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  flex-direction: row !important;
}

.header-nav .nav-btn {
  flex: 1;
  background: #EFEFEF;
  color: #252525;
  border: 1px solid #cccccc;
  border-radius: 0;
  padding: 1% 0;
  font-size: 80%;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  transition: background 0.2s;
}

.header-nav .nav-btn:hover {
  background: #dedede;
}

.header-nav .nav-btn-inner {
  display: block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.header-nav .nav-btn-label {
  display: inline-block;
  text-align: center;
  margin: 0 auto;
}

.header-nav .nav-btn i {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-size: 150%;
}

footer {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 12px 0 12px 0;
  text-align: center;
  color: #252525;
  font-size: 0.95rem;
}

section,
main {
  width: 100%;
  margin: 0 auto;
  max-width: 1024px;
  box-sizing: border-box;
  font-size: 90%;
}

main {
  padding: 0 0.5% 1% 0.5%;
  border-right: 1.5px solid #e0e0e0;
  border-left: 1.5px solid #e0e0e0;
  border-bottom: 1.5px solid #e0e0e0;
}

section {
  padding: 0 0 0 0;
  border-top: 1.5px solid #e0e0e0;
  /*border-bottom: 1.5px solid #e0e0e0;*/
  box-shadow: none;
}

.home-intro {
  text-align: center;
}

.external-links-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 1vw;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 32px;
}

.external-link-box {
  max-width: 300px;
  width: 100%;
  flex: 1 1 28%; /* 3つ並ぶように調整 */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fAfAfA;
  border: 2px solid #cccccc;
  border-radius: 12px;
  padding: 24px 12px;
  font-size: 1.1rem;
  font-weight: 600;
  color: #252525;
  text-align: center;
  text-decoration: none;
  transition: box-shadow 0.2s, border-color 0.2s;
  margin-bottom: 0;
  cursor: pointer;
}

.external-link-box:hover {
  border-color: #888;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
}

.aboutme-flex {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.aboutme-flex img {
  max-width: 300px;
  width: 100%;
  padding: 0 2.5%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.aboutme-flex .home-intro {
  flex: 1 1 200px;
  min-width: 200px;
}

.aboutme-title {
  color: #444;
}

a {
  color: #1976d2;
  text-decoration: underline;
  transition: color 0.2s;
}

a:hover {
  color: #ea5320;
}

/* Tab button styles (distinct from genre buttons) */
.tab-btn {
  background: #dedede;
  border: none;
  padding: 14px 22px;
  border-radius: 12px 12px 0 0;
  cursor: pointer;
  font-weight: 700;
  color: #374151; /* slate-700 */
  font-size: 1.05rem;
  margin: 0 4px;
  position: relative;
  transition: color 160ms ease, background 160ms ease, transform 160ms ease, box-shadow 160ms ease;
  border-bottom: 3px solid transparent;
}

.tab-btn:hover {
  color: #111827; /* slate-900 */
  background: rgba(15,23,42,0.04);
  transform: translateY(0px);
}

.tab-btn:focus {
  outline: 3px solid rgba(59,130,246,0.18);
  outline-offset: 2px;
}

.tab-btn.active {
  color: #ffffff;
  background: linear-gradient(45deg,#ff7a18,#ffa646);
  box-shadow: 0 8px 24px rgba(255,135,60,0.18);
  border-bottom-color: transparent;
}

/* make tab buttons visually grouped */
.tab-btn:first-child {
  margin-left: 0;
}

.tab-btn:last-child {
  margin-right: 0;
}

/* section title icon */
.section-title-wrap {
  display: flex;
  align-items: center;
  gap: 12px;
  justify-content: center;
}

.section-icon {
  font-size: 28px;
  color: inherit; /* 見出しの文字色に合わせる */
}

/* Header/Nav icons and external link icons inherit text color */
.nav-icon,
.external-link-box i {
  color: inherit;
}

/* Home 最新情報欄の左右マージン */
.home-latest-news-section {
  margin: 0 5%;
}

/* Contentsジャンルフィルタボタン */
.genre-toggle-btn {
  background: #fAfAfA;
  color: #444;
  border: 1.5px solid #ccc;
  border-radius: 20px;
  padding: 6px 18px;
  margin: 4px 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border-color 0.2s;
}

.genre-toggle-btn.active {
  background: #ea5320;
  color: #fff;
  border-color: #ea5320;
}

.genre-toggle-btn:focus {
  outline: 2px solid #ea5320;
  outline-offset: 2px;
}

/* Streaming card styles (desktop: horizontal) */
.streaming-card {
  display: grid;
  grid-template-columns: 160px 1fr 360px; /* image | meta | tracks */
  grid-template-rows: auto auto; /* top row for meta/tracks, second row for links */
  grid-template-areas:
    "image meta tracks"
    "image links links";
  row-gap: 10px;
  column-gap: 16px;
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 12px;
  padding: 12px;
  box-sizing: border-box;
  width: 100%;
  max-width: 1000px;
  min-width: 320px;
  margin: 8px;
  align-items: start;
}

/* jacket image */
.streaming-card .streaming-image {
  grid-area: image;
  width: 160px;
  height: 160px;
  object-fit: cover;
  border-radius: 8px;
  flex: 0 0 160px; /* fixed column */
}

/* album title / artist / year / small meta block */
.streaming-card .streaming-meta {
  grid-area: meta;
  font-size: 0.95rem;
  color: #444;
  padding-top: 6px;
  padding-left: 2%;
  padding-right: 2%;
}

.streaming-card .streaming-title{
  font-weight: bold;
}

/* tracks list: grows to fill available space */
.streaming-card .streaming-tracks {
  height: 78px;
  margin: 0;
  /* keep numbering inside but ensure it doesn't expand the card */
  padding-left: 18px; /* visual indent for numbering */
  list-style-position: inside;
  padding-left: 0; /* we use inside numbering */
  margin: 0;

  /* limit the visible height and make this region scrollable when content overflows */
  max-height: 300px;
  overflow: auto;

  /* prevent the tracks column from forcing the card wider */
  width: 100%;
  box-sizing: border-box;
  min-width: 0;

  /* ensure long track titles wrap instead of expanding width */
  word-break: break-word;
  overflow-wrap: break-word;
  white-space: normal;
}

.streaming-tracks-label {
  display: block;
  text-align: center;
  font-weight: 700;
  /*margin: 6px 0 8px 0;*/
}

/* links area: 3-column grid, buttons stretch to fill columns, rectangular */
.streaming-card .streaming-links {
  grid-area: links;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  align-items: stretch;
  justify-items: stretch;
}

/* stream link buttons: rectangular and fill column */
.streaming-card .stream-link {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 8px;
  align-items: center;
  padding: 10px 12px;
  border-radius: 6px; /* slightly rounded rectangle */
  background: #dddddd;
  color: #111;
  text-decoration: none;
  font-weight: 600;
  box-sizing: border-box;
}

.streaming-card .stream-link i {
  font-size: 1.05em;
}
/* Stream link buttons - service colors */
  .stream-link {
    background: rgba(255,255,255,0.03);
    color: #e6e6e6;
  }
  .stream-link i {
    color: inherit;
  }
  .stream-link.apple {
    background: #000; /* Apple: dark */
    color: #ffffff;
  }
  .stream-link.spotify {
    background: #1DB954; /* Spotify brand green */
    color: #ffffff;
  }
  .stream-link:hover {
    filter: brightness(1.02);
    /*transform: translateY(-2px);*/
  }
  .stream-link.apple:hover {
    background: #111;
  }
  .stream-link.spotify:hover {
    filter: brightness(0.95);
  }
  .stream-link.other:hover {
    background: rgba(255,255,255,0.06);
  }

/* Mobile: stack vertically under 750px */
@media (max-width: 750px) {
  .streaming-card {
    display: flex;
    flex-direction: column;
    width: calc(100% - 24px);
    max-width: 100%;
    align-items: stretch;
    padding: 0px;
    gap: 8px;
  }
  .streaming-card .streaming-image {
    width: 100%;
    height: auto;
    margin-bottom: 8px;
    flex: none;
  }

  .streaming-card .streaming-tracks {
    max-height: 220px; /* allow more room for numbered list */
    overflow: auto;
    padding-left: 18px;
  }
  .streaming-card .streaming-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
  }
  .streaming-card .stream-link {
    width: auto;
    flex: 1 1 calc(50% - 8px);
  }
}

/* Desktop tweak: ensure good spacing on slightly larger screens */
@media (min-width: 601px) {
  .streaming-card {
    align-items: flex-start;
  }
  .streaming-card .streaming-meta {
    padding-top: 6px;
  }
}

/* ==========================
   Dark mode / prefers-color-scheme
   (single merged block for all dark-mode adjustments)
   ========================== */
@media (prefers-color-scheme: dark) {
  body {
    background: #222;
    color: #eee;
  }
  a {
    color: #90caf9;
  }
  a:hover {
    color: #ea5320;
  }
  section,
  main,
  .external-link-box,
  footer {
    background: #222;
    color: #eee;
    border-color: #444;
  }
  .header-nav .nav-btn {
    background: #333;
    color: #eee;
    border-color: #444;
  }
  .header-nav .nav-btn:hover {
    background: #444;
  }
  .aboutme-flex img {
    box-shadow: 0 2px 8px #222;
  }
  .home-latest-news-section {
    background: #222;
    color: #eee;
    border-color: #444;
  }
  header svg {
    fill: #ffffff !important;
    stroke: #ffffff !important;
    color: #ffffff !important;
  }
  .aboutme-title {
    color: #eee;
  }
  .genre-toggle-btn:focus {
    outline: 2px solid #ea5320;
    outline-offset: 2px;
  }
  .tab-btn {
    color: #ffffff; /* slate-700 */
    background: #333;
  }

  .tab-btn:hover {
    color: #ea5320; /* slate-900 */
    background: rgba(70, 70, 70, 1);
    transform: translateY(0px);
  }

  .tab-btn.active {
    color: #ffffff;
    background: linear-gradient(45deg,#ff7a18,#ffa646);
    box-shadow: 0 8px 24px rgba(255,135,60,0.18);
    border-bottom-color: transparent;
  }

  .tab-btn:focus {
    outline: 3px solid rgba(59,130,246,0.18);
  }

  /* Streaming card - dark mode adjustments */
  .streaming-card,
  .streaming-card.vertical {
    background: #333;
    border-color: #2b2b2b;
    color: #e6e6e6;
    box-shadow: 0 6px 18px rgba(0,0,0,0.6);
  }
  .streaming-card .streaming-title,
  .streaming-author,
  .streaming-card.vertical .streaming-title {
    color: #ffffff;
  }
  .streaming-meta,
  .streaming-year,
  .streaming-tracks li {
    color: #cfcfcf;
  }
  .streaming-card .streaming-image,
  .streaming-card.vertical .streaming-image {
    background: #222;
  }
  .stream-link.other {
    background: rgba(255,255,255,0.04);
    color: #e6e6e6;
  }

  /* Blog-specific dark mode adjustments */
  .blog-page,
  .blog-grid,
  .blog-list,
  .blog-content {
    color: #e6e6e6;
  }

  /* cards for list items use the same dark card style as streaming-card */
  .blog-list .youtube-video > div,
  .markdown-article,
  .blog-empty {
    background: #333;
    border: 1px solid #2b2b2b;
    color: #e6e6e6;
    box-shadow: 0 6px 18px rgba(0,0,0,0.6);
  }

  .blog-list .youtube-video {
    /* ensure clickable card wrapper doesn't change background */
    background: transparent;
  }

  .blog-title {
    color: #ffffff;
  }
  .blog-date {
    color: #bdbdbd;
  }
  .blog-excerpt {
    color: #cfcfcf;
  }

  /* markdown content tweaks */
  .markdown-article a {
    color: #90caf9;
  }
  .markdown-article pre,
  .markdown-article code {
    background: #0f1112;
    color: #f2f2f2;
    border-radius: 6px;
    padding: 6px;
  }
  .markdown-article blockquote {
    border-left: 4px solid #2b2b2b;
    color: #d0d0d0;
    background: rgba(255,255,255,0.02);
    padding: 8px 12px;
    border-radius: 6px;
  }

  /* scrollbars for markdown/article */
  .markdown-article::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,0.08);
    border-radius: 8px;
  }
  .markdown-article {
    scrollbar-color: rgba(255,255,255,0.08) transparent;
  }
}

/* ==========================
   Media queries consolidated
   (grouped from larger -> smaller)
   ========================== */

/* Narrower than ~1100px: single column youtube videos */
@media (max-width: 1100px) {
  .youtube-video {
    flex-basis: 100%; /* 狭い画面では1列 */
    min-width: 0;
  }
}

/* External link boxes: stack to 2-column at narrower widths */
@media (max-width: 1000px) {
  .external-link-box {
    flex-basis: 45%; /* 2列 */
  }
}

/* Streaming grid behavior */
@media (min-width: 900px) {
  .streaming-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    justify-content: center;
  }
}
/*@media (max-width: 900px) {
  .streaming-grid {
    display: block;
  }
  .streaming-card {
    width: 500px;
    max-width: 500px;
  }
}
*/

/* Mobile specific rules (<=600px): vertical streaming cards, external links 2-column fallback */
@media (max-width: 750px) {
  .streaming-card {
    flex-direction: column;
    width: calc(100% - 24px);
    max-width: 100%;
    align-items: stretch;
    margin: 5% auto;
  }
  .streaming-card .streaming-image {
    width: 100%;
    /*height: 260px;*/
    margin-bottom: 10px;
    flex: none;
  }
  .streaming-title {
    font-size: 1.05rem;
    margin-bottom: 6px;
  }
  .streaming-meta {
    margin-bottom: 6px;
  }
  .streaming-year {
    font-weight: 700;
    color: #666;
    margin-bottom: 8px;
  }
  .streaming-tracks {
    max-height: 220px;
    overflow: auto;

    /* same wrapping/box-sizing rules for mobile */
    padding-left: 18px;
    box-sizing: border-box;
    min-width: 0;
    word-break: break-word;
    overflow-wrap: break-word;
    white-space: normal;
  }

  /* external links on smaller screens: maintain comfortable width */
  .external-link-box {
    flex-basis: 45%; /* preserve prior behavior */
  }
}

/* Small header/nav adjustments for very narrow screens */
@media (max-width: 500px) {
  .header-nav {
    flex-direction: row !important;
  }
  .header-nav .nav-btn-inner {
    flex-direction: column;
  }
  .header-nav .nav-btn-label {
    display: block;
    font-size: 90%;
    margin-top: 2px;
  }
  .header-nav .nav-btn i {
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 150%;
  }
}

/* markdown article images: responsive within article */
.markdown-article img {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 6px;
}

.markdown-article p {
  font-size: 1.2em;
  line-height: 2em;
}


/* blog list thumbnail: same size as streaming jacket on desktop, full width on small screens */
.blog-list img {
  width: 160px;
  height: 160px;
  object-fit: cover;
  border-radius: 8px;
}

@media (max-width: 750px) {
  .blog-list img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}

/* Blog post meta row: horizontal on wide screens, vertical on small screens */
.blog-meta-row {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 12px;
  flex-wrap: wrap;
}
.blog-date {
  color: #6b7280;
  font-weight: 600;
  min-width: 120px;
}
.blog-tags {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.blog-tag {
  background: #f3f4f6;
  padding: 4px 10px;
  border-radius: 999px;
  font-weight: 700;
  color: #374151;
  font-size: 0.9rem;
}

/* new: percentage layout for title/date/tags on wide screens */
.blog-meta-row {
  flex-wrap: nowrap;
  gap: 0; /* remove gap between columns as requested */
}
/* Title should expand/contract to fill available space */
.blog-title-main {
  flex: 1 1 58%; /* allow grow and shrink, base 58% */
  max-width: 100%;
  margin: 0;
  font-size: 1.5rem;
}
/* Date and tags keep their base percentages but may shrink; ensure minimum widths for readability */
.blog-date {
  flex: 0 1 10%;
  min-width: 70px;
  text-align: left;
}
.blog-tags {
  flex: 0 1 30%;
  min-width: 150px;
  justify-content: flex-end;
}

@media (max-width: 500px) {
  .blog-meta-row {
    flex-direction: column;
    align-items: flex-start;
    gap:0.8em;
  }
  .blog-title-main {
    flex: 1 1 58%; /* allow grow and shrink, base 58% */
    max-width: 100%;
    margin: 0;
    font-size: 1.5rem;
  }
/* Date and tags keep their base percentages but may shrink; ensure minimum widths for readability */
  .blog-date {
    flex: 0 1 100%;
    max-width: 100%;
    min-width: 70px;
    text-align: left;
  }
  .blog-tags {
    flex: 0 1 100%;
    max-width: 100%;
    justify-content: flex-end;
  }
}

/* Markdown content images (blog / lyrics) - cap size and center */
.markdown-article img,
.markdown-article p img {
  display: block;
  max-width: 500px; /* never exceed 500px */
  width: 100%;      /* shrink to container when needed */
  height: auto;
  border-radius: 8px;
}
